html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.v-cart {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.v-cart__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  padding: 0 20px;
  margin: 0;
  border: 0;
  border-radius: 5px;
  min-width: 140px;
  height: 60px;
  color: #fff;
  background: #9c7fff;
  font-family: inherit;
  text-align: center;
  cursor: pointer;
}

.v-cart__btn::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.v-cart__btn-figure {
  font-size: 0;
}

.v-cart__btn-icon {
  fill: #fff;
  width: 36px;
}

.v-cart__btn-title, .v-cart__btn-subtitle {
  display: block;
}

.v-cart__btn-title {
  font-size: 18px;
}

.v-cart__btn-subtitle {
  font-size: 12px;
}

.v-cart__btn-content {
  padding-left: 15px;
}

.v-cart__dropdown {
  position: absolute;
  top: 100%;
  left: 50%;
  padding: 7px 12px;
  margin-top: 10px;
  border-radius: 2px;
  width: 260px;
  background: #fff;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  line-height: 1.2;
  transform: translateX(-50%);
}

.v-cart__item {
  padding: 8px 5px;
}

.v-cart__item:not(:last-child) {
  border-bottom: 1px solid #000;
}

.v-cart__error {
  padding: 2px 5px 5px;
  color: red;
  font-size: 12px;
}
